@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap'); // na wszelki wypadek

$fg: #121212;
$primary: #E4C1AE;

body {
    font-family: "Roboto", sans-serif;
    margin: 0;
    position: relative;
}

.container {
    max-width: 1440px;
    margin: 0 auto;
    padding-top: 96px;
}

.compotent {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 96px auto;
    gap: 95px;

    .img {
        display: flex;
        justify-content: center;
        max-width: 505px;

        img {
            width: 100%;
            height: auto;
        }
    }

    .content {
        width: 380px;
        color: $fg;

        h2 {
            font-size: 42px;
            line-height: 50.4px;
            margin: 0 0 24px 0;
        }

        p {
            font-weight: 300;
            font-size: 16px;
            line-height: 24px;
            margin: 0;
        }
    }
}

.button {
    padding: 16px 40px;
    border-radius: 24px;
    font-size: 16px;
    line-height: 16px;
    background-color: $primary;
    color: white;
    font-weight: 700;
    border: none;
    margin-top: 32px;
    transition: background-color 200ms;

    &:hover {
        background-color: darken($primary, 5%);
    }

    &:focus {
        background-color: lighten($primary, 5%);
    }
}

.modal {
    position: fixed;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;

    .bg {
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: $fg;
        opacity: 0.5;
        z-index: 1;
    }

    .content {
        background-color: white;
        z-index: 2;
        padding: 56px 95px;
        position: relative;
        width: clamp(360px, 920px, 50%);

        .close {
            position: absolute;
            top: 48px;
            right: 48px;
            width: 24px;
            height: 24px;
            background: url("/images/x-icon.svg");
            border: none;
            transition: opacity 100ms;

            &:hover {
                opacity: 0.3;
            }
        }

        h2 {
            margin: 0;
            font-weight: 700;
            font-size: 42px;
            line-height: 120%;
        }

        p {
            margin: 24px 0 0 0;
            font-weight: 300;
            font-size: 16px;
            line-height: 150%;
        }
    }
}

.hidden {
    display: none;
}

@media only screen and (max-width: 1100px) {
    .container {
        padding: 10px;
        box-sizing: border-box;
    }

    .compotent {
        max-width: 505px;
        flex-direction: column;
        gap: 36px;
        overflow: hidden;
        margin: 36px auto;

        .content {
            width: 100%;
            padding: 10px;
            box-sizing: border-box;

            button {
                display: block;
                margin: 36px auto;
            }

            h2 {
                font-size: 26px;
                position: relative;
                top: -110px;
                left: 20px;
                color: white;
                margin-bottom: -50px
            }
        }

        .img {
            width: 100%;
            overflow: hidden;
            position: relative;

            .shadow {
                position: absolute;
                bottom: 0;
                width: 100%;
                height: 150px;
                background: linear-gradient(to top, #000b, #0000);
            }
        }
    }

}